import { Entity, Column, PrimaryGeneratedColumn, OneToOne, JoinColumn  } from 'typeorm'
import { ObjectType, Field, ID } from "type-graphql";
import LectureContentEntity from './LectureContentEntity';

@Entity({ name: 'TB_LECTURE_MASTER' })
@ObjectType()
export default class LectureEntity {
	
	@PrimaryGeneratedColumn({ name: 'CONTENT_NO' })
	@Field(()=>ID)
	content_no?: string

	@Column({ name: 'UPPER_CONTENT_NO' })
	@Field(()=>String)
	upper_content_no?: string

	@Column({ name: 'CONTENT_TITLE' })
	@Field(()=>String)
	content_title?: string

	@Column({ name: 'CONTENT_REGIST_DATE' })
	@Field()
	content_regist_date?: Date

	@Column({ name: 'TITLE_IMAGE_PATH' })
	@Field(()=>String)
	title_image_path?: string

	@Column({ name: 'CONTENT_OUTLINE' })
	@Field(()=>String)
	content_outline?: string

	@OneToOne(()=>LectureContentEntity)
	@JoinColumn()
	lecture_detail?: LectureContentEntity
	


	constructor({
		content_no, upper_content_no, 
		content_title,  title_image_path, content_regist_date,
		content_outline, 
		lecture_detail,
	}: LectureEntity){
		this.content_no = content_no 
		this.upper_content_no = upper_content_no 
		this.content_title = content_title  
		this.title_image_path = title_image_path 
		this.content_regist_date = content_regist_date 
		this.content_outline = content_outline  		
		this.lecture_detail = lecture_detail
	}
}

export { LectureEntity }